import { Component, ChangeDetectionStrategy, ElementRef, ViewChild } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import {Router} from '@angular/router';
import 'rxjs/add/operator/toPromise';

//import { ModalDirective } from 'ngx-bootstrap/modal/modal.component'; //modales
import { ModalDirective } from 'ngx-bootstrap/modal';

import { RutaService } from '../../../services/ruta-service/ruta.service';

import { FormBuilder, FormArray, FormGroup, Validators  } from '@angular/forms';

//import {NgxPermissionsService, NgxRolesService} from 'ngx-permissions';

//import { NgxGalleryOptions, NgxGalleryImage, NgxGalleryAnimation } from 'ngx-gallery';

//import { CartService } from '../../../services/cart-service/cart.service';

@Component({
  templateUrl: 'home.component.html',
  styleUrls: ['home.component.css']
})
export class HomeComponent {

  private data:any;
  private data2:any;

  public loading = false;
  public editando = false;
  public agregando = false;
  public mostrar = true;
  public detalle = false;

  public alerta = false;
  public alerta_boton = false;
  public alerta_tipo: any; //success info warning danger  
  public alerta_msg: any;

  public categorias:any;
  public ourProducts:any;
  public masVistos:any;
  public masVendidos:any;
  public blogs:any;


  constructor(private http: HttpClient,
      private router: Router,
      private rutaService: RutaService,
      public fb: FormBuilder/*,
      private cartService: CartService*/) {

  }

  ngOnInit(): void {

    if(this.router.navigated) {
      window.scrollTo(0, 0);
    }
    
      this.getCategorias();
  }

  slides = [
    {img: "http://placehold.it/350x150/000000"},
    {img: "http://placehold.it/350x150/111111"},
    {img: "http://placehold.it/350x150/333333"},
    {img: "http://placehold.it/350x150/666666"}
  ];
  slideConfig = {"slidesToShow": 4, "slidesToScroll": 4};

  addSlide() {
    this.slides.push({img: "http://placehold.it/350x150/777777"})
  }

  removeSlide() {
    this.slides.length = this.slides.length - 1;
  }

  afterChange(e) {
    console.log('afterChange');
  }

  //Alerta cuando se vence el token
  ok_alerta(): void {
    this.router.navigate(['luvas']);
  }

  cerrarAlerta(): void {
    this.alerta = false
  }

  /*Trae las categorias en estado ON*/
  getCategorias(): void {
    this.loading = true;
    this.http.get(this.rutaService.getRutaApi()+'categorias/home/cliente?token='+localStorage.getItem('luvas_token'))
       .toPromise()
       .then(
         data => { // Success
           console.log(data);
           this.data2 = data;
           this.categorias=this.data2.categorias;
           this.loading = false; 

           this.getProductsPopulares();   
         },
         msg => { // Error
           msg.error = JSON.parse(msg.error);
           console.log(msg);
           console.log(msg.error.error);
           this.loading = false;

           //token invalido/ausente o token expiro
           if(msg.status == 400 || msg.status == 401){ 
                //alert(msg.error.error);
                //ir a login

                this.alerta_tipo = 'warning';
                this.alerta_msg = msg.error.error;
                this.alerta_boton = true;
                this.mostrar = false;

            }
            else{ 
                //alert(msg.error.error);

                this.alerta_tipo = 'info';
                this.alerta_msg = msg.error.error;
                this.alerta = true;
                setTimeout(()=>{this.alerta = false;},4000);
            }

         }
       );
    }

    selectCategoria(categoria){
      var cat = JSON.stringify(categoria)
      localStorage.setItem('luvas_catFilter', cat);
      this.router.navigateByUrl('/luvas/productos');
    }

  /*Trae los productos mas vistos y mas populares*/
  getProductsPopulares(): void {
    //this.loading = true;
    this.http.get(this.rutaService.getRutaApi()+'productos/mas/populares?token='+localStorage.getItem('luvas_token'))
       .toPromise()
       .then(
         data => { // Success
           console.log(data);
           this.data = data;
           
           this.masVistos = this.data.masVistos;
           this.masVendidos = this.data.masVendidos;
           
           //this.loading = false;
           this.getBlogs();    
         },
         msg => { // Error
           msg.error = JSON.parse(msg.error);
           console.log(msg);
           console.log(msg.error.error);
           //this.loading = false;

           //token invalido/ausente o token expiro
           if(msg.status == 400 || msg.status == 401){ 
                //alert(msg.error.error);
                //ir a login

                this.alerta_tipo = 'warning';
                this.alerta_msg = msg.error.error;
                this.alerta_boton = true;
                this.mostrar = false;

            }
            else{ 
                //alert(msg.error.error);

                this.alerta_tipo = 'info';
                this.alerta_msg = msg.error.error;
                this.alerta = true;
                setTimeout(()=>{this.alerta = false;},4000);
            }

         }
       );
    }

    selectProducto(producto){
      var prod = JSON.stringify(producto)
      localStorage.setItem('luvas_prodFilter', prod);
      this.router.navigateByUrl('/luvas/productos');
    }

  /*Trae los ultimos 3 blogs*/
  getBlogs(): void {
    //this.loading = true;
    this.http.get(this.rutaService.getRutaApi()+'blogs/home/cliente?token='+localStorage.getItem('luvas_token'))
       .toPromise()
       .then(
         data => { // Success
           console.log(data);
           this.data = data;
           
           this.blogs = this.data.blogs;
           
           //this.loading = false;    
         },
         msg => { // Error
           msg.error = JSON.parse(msg.error);
           console.log(msg);
           console.log(msg.error.error);
           //this.loading = false;

           //token invalido/ausente o token expiro
           if(msg.status == 400 || msg.status == 401){ 
                //alert(msg.error.error);
                //ir a login

                this.alerta_tipo = 'warning';
                this.alerta_msg = msg.error.error;
                this.alerta_boton = true;
                this.mostrar = false;

            }
            else{ 
                //alert(msg.error.error);

                this.alerta_tipo = 'info';
                this.alerta_msg = msg.error.error;
                this.alerta = true;
                setTimeout(()=>{this.alerta = false;},4000);
            }

         }
       );
    }

    selectBlog(blog){
      var blogAux = JSON.stringify(blog)
      localStorage.setItem('luvas_blogFilter', blogAux);
      this.router.navigateByUrl('/luvas/blog');
    }

}



