  buyItem() {

    /*Valida la cantidad
    introducida manualmente*/
    if (this.cantidad_select < 1) {
      if (this.cantidad_max > 0) {
        this.cantidad_select = 1;
      }else{
        this.cantidad_select = 0;
      }  
    }

    if (this.cantidad_select > this.cantidad_max) {
      this.cantidad_select = this.cantidad_max;
    }

    setTimeout(()=>{

      //console.log(this.producto);
      //let item_aux = Object.assign({},JSON.stringify(obj));
      let item = JSON.parse(JSON.stringify(this.producto));
      let color = JSON.parse(JSON.stringify(this.color_select));
      let talla = JSON.parse(JSON.stringify(this.talla_select));
      //console.log(color);
      //console.log(talla);

      item.colores = [color];
      item.colores[0].atributos = [talla];
      console.log(item);

      this.cartService.addProduct(item, this.cantidad_select).subscribe(
        success => {
          if (success) {
            this.itemsInCart = this.cartService.getCartCount();
            //alert('Producto agregado!');
            this.alerta_tipo = 'success';
            this.alerta_msg = 'Producto agregado!';
            this.alerta = true;
            setTimeout(()=>{this.alerta = false;},4000);
            //this.presentCheckout();
            this.showModal();

            this.cartEventService.cartEventData.emit(this.itemsInCart);

          } else {

            //alert('Ha ocurrido un error intenta de nuevo');
            this.alerta_tipo = 'warning';
            this.alerta_msg = 'Ha ocurrido un error intenta de nuevo';
            this.alerta = true;
            setTimeout(()=>{this.alerta = false;},4000);
          }
        },
        error => {
          //alert(error);
          this.alerta_tipo = 'danger';
          this.alerta_msg = error;
          this.alerta = true;
          setTimeout(()=>{this.alerta = false;},4000);
        }
      );

    },60);
  }

