import { Component, ChangeDetectionStrategy, ElementRef, ViewChild, OnDestroy } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import {Router} from '@angular/router';
import 'rxjs/add/operator/toPromise';

//import { ModalDirective } from 'ngx-bootstrap/modal/modal.component'; //modales
import { ModalDirective } from 'ngx-bootstrap/modal';

import { RutaService } from '../../../services/ruta-service/ruta.service';

import { FormBuilder, FormArray, FormGroup, Validators  } from '@angular/forms';

//import {NgxPermissionsService, NgxRolesService} from 'ngx-permissions';

import { NgxGalleryOptions, NgxGalleryImage, NgxGalleryAnimation } from 'ngx-gallery';

import { CartService } from '../../../services/cart-service/cart.service';

import { LoginEventService } from '../../../events/login-event/login-event.service';


@Component({
  templateUrl: 'pedidoscli.component.html',
  styleUrls: ['pedidoscli.component.css']
})
export class PedidosCliComponent {

  //Modal automatica
  @ViewChild('autoShownModal') public autoShownModal:ModalDirective;
  public isModalShown:boolean = false;
 
  public showModal():void {
    this.isModalShown = true;
  }
 
  public hideModal():void {
    this.autoShownModal.hide();
  }
 
  public onHidden():void {
    this.isModalShown = false;
  }

  //Modal automatica Seleccion de metodo de pago
  @ViewChild('autoShownModal2') public autoShownModal2:ModalDirective;
  public isModalShown2:boolean = false;
 
  public showModal2():void {
    this.isModalShown2 = true;
  }
 
  public hideModal2():void {
    this.autoShownModal2.hide();
  }
 
  public onHidden2():void {
    this.isModalShown2 = false;
  }

  //public myModal;
  @ViewChild('myModal2') public myModal2:ModalDirective; //Pago por transferencia
  @ViewChild('myModal3') public myModal3:ModalDirective; //Pago por PayPal


  public items: any;
  public itemsInCart: number;
  public total: number;

  public loading = false;
  private data:any;

  public mostrar = true;
  public viendo = false;

  public alerta = false;
  public alerta_boton = false;
  public alerta_tipo: any; //success info warning danger  
  public alerta_msg: any;

  //Formularios
  myFormLogin: FormGroup;
  myFormPago: FormGroup;
  myFormUpdateProfile: FormGroup;

  public pedidos:any;
  selectedObj: any;

  public token:any;
  public user:any;

  public bancos = [];

  //Imagenes
  @ViewChild('fileInput') fileInput: ElementRef;
  clear = false; //puedo borrar?
  fileIMG = null;
  imgUpload = null;
  loadinImg = false;

  constructor(private http: HttpClient,
      private router: Router,
      private rutaService: RutaService,
      public fb: FormBuilder,
      private cartService: CartService,
      public loginEventService: LoginEventService) {

    this.items = this.cartService.getCartContents();
    //console.log(this.items);

    this.myFormLogin = this.fb.group({
        email: ['', [Validators.required]],
        password: ['', [Validators.required]]
      });

    this.myFormPago = this.fb.group({
        ref_pago: ['', [Validators.required]],
        metodo_pago: [0],
        pedido_id: [0]
      });

    this.myFormUpdateProfile = this.fb.group({
        email: ['', [Validators.required]],
        password: [''],
        confirm: [''],
        nombre: ['', [Validators.required]],
        telefono: ['', [Validators.required]],
        direccion: ['', [Validators.required]],
        imagen: ['']
      });

  }

  ngOnInit(): void {

    if(this.router.navigated) {
      window.scrollTo(0, 0);
    }

    /*Verificar si hay un usuario logeado*/
    this.token = localStorage.getItem('luvas_token');
    var user_aux = localStorage.getItem('luvas_user');
    var user_login = JSON.parse(user_aux);

    if (!user_login || user_login == 'null' || !this.token || this.token == 'null') {
      this.showModal();
    }else{
      this.user = user_login;
      this.getMisPedidos();
      this.setFormProfile();
    }
  }

  slides = [
    {img: "http://placehold.it/350x150/000000"},
    {img: "http://placehold.it/350x150/111111"},
    {img: "http://placehold.it/350x150/333333"},
    {img: "http://placehold.it/350x150/666666"}
  ];
  slideConfig = {"slidesToShow": 4, "slidesToScroll": 4};

  addSlide() {
    this.slides.push({img: "http://placehold.it/350x150/777777"})
  }

  removeSlide() {
    this.slides.length = this.slides.length - 1;
  }

  afterChange(e) {
    console.log('afterChange');
  }

  //Alerta cuando se vence el token
  ok_alerta(): void {
    this.router.navigate(['luvas']);
  }

  cerrarAlerta(): void {
    this.alerta = false
  }

  getMisPedidos(){
    this.loading = true;

      this.http.get(this.rutaService.getRutaApi()+'pedidos/usuario/'+this.user.id+'?token='+localStorage.getItem('luvas_token'))
         .toPromise()
         .then(
           data => { // Success
             console.log(data);

             this.data=data;
             var dia : any;
             var mes : any;
             var anio : any;
             var fecha : any;
             var hora : any;
             var minutos : any;
             var segundos : any;

             if (this.data.pedidos.length == 0) {
                this.alerta_tipo = 'info';
                this.alerta_msg = 'You have no registered orders';
                this.alerta = true;
                setTimeout(()=>{this.alerta = false;},4000);
             }


             for (var i = 0; i < this.data.pedidos.length; i++) {
               fecha = new Date(this.data.pedidos[i].created_at);
               //this.data.pedidosHoy[i].created_at = new Date(this.data.pedidosHoy[i].created_at);
               dia = fecha.getDate();
               mes = fecha.getMonth() + 1;
               anio = fecha.getFullYear();

               hora = fecha.getHours();
               minutos = fecha.getMinutes();
               segundos = fecha.getSeconds();

               this.data.pedidos[i].fecha = dia+'/'+mes+'/'+anio;
               this.data.pedidos[i].hora = hora+':'+minutos+':'+segundos;

               if (this.data.pedidos[i].estado == 1) {
                 this.data.pedidos[i].estado = 'Pending';
               }else if (this.data.pedidos[i].estado == 2) {
                 this.data.pedidos[i].estado = 'Payment';
               }else if (this.data.pedidos[i].estado == 3) {
                 this.data.pedidos[i].estado = 'Delivery';
               }else if (this.data.pedidos[i].estado == 4) {
                 this.data.pedidos[i].estado = 'Cancel';
               }
             }

             setTimeout(()=>{
               this.loading = false;
               this.pedidos = this.data.pedidos;
             },1000);
           },
           msg => { // Error
             msg.error = JSON.parse(msg.error);
             console.log(msg);
             console.log(msg.error.error);

             this.loading = false;

             //token invalido/ausente o token expiro
             if(msg.status == 400 || msg.status == 401){ 
                  //alert(msg.error.error);
                  //ir a login

                  this.alerta_tipo = 'warning';
                  this.alerta_msg = msg.error.error;
                  this.alerta_boton = true;
                  this.mostrar = false;

              }
              else { 
                  //alert(msg.error.error);

                  this.alerta_tipo = 'danger';
                  this.alerta_msg = msg.error.error;
                  this.alerta = true;
                  setTimeout(()=>{this.alerta = false;},4000);
              }

           }
         );
  }

    ver(obj): void {

      /*Verificar si hay un usuario logeado*/
      this.token = localStorage.getItem('luvas_token');
      var user_aux = localStorage.getItem('luvas_user');
      var user_login = JSON.parse(user_aux);

      if (!user_login || user_login == 'null' || !this.token || this.token == 'null') {
        this.showModal();
      }else{
        this.user = user_login;

        this.loading = true;

        this.http.get(this.rutaService.getRutaApi()+'pedidos/'+obj.id+'?token='+localStorage.getItem('luvas_token'))
           .toPromise()
           .then(
             data => { // Success
               console.log(data);

               this.data=data;
               this.selectedObj = this.data.pedido;
               this.myFormPago.patchValue({pedido_id : this.selectedObj.id});

               if (this.data.pedido.estado == 1) {
                 this.data.pedido.estado = 'Pending';
               }else if (this.data.pedido.estado == 2) {
                 this.data.pedido.estado = 'Payment';
               }else if (this.data.pedido.estado == 3) {
                 this.data.pedido.estado = 'Delivery';
               }else if (this.data.pedido.estado == 4) {
                 this.data.pedido.estado = 'Cancel';
               }

               if (this.data.pedido.metodo_pago == 1) {
                 this.data.pedido.metodo_pago = 'Transfer';
               }else if (this.data.pedido.metodo_pago == 2) {
                 this.data.pedido.metodo_pago = 'PayPal';
               }

               this.viendo = true;
               this.loading = false;
               window.scrollTo(0, 0);
               
             },
             msg => { // Error
               msg.error = JSON.parse(msg.error);
               console.log(msg);
               console.log(msg.error.error);

               this.loading = false;

               //token invalido/ausente o token expiro
               if(msg.status == 400 || msg.status == 401){ 
                    //alert(msg.error.error);
                    //ir a login

                    this.alerta_tipo = 'warning';
                    this.alerta_msg = msg.error.error;
                    this.alerta_boton = true;
                    this.mostrar = false;

                }
                else { 
                    //alert(msg.error.error);

                    this.alerta_tipo = 'danger';
                    this.alerta_msg = msg.error.error;
                    this.alerta = true;
                    setTimeout(()=>{this.alerta = false;},4000);
                }

             }
           );
      }
    }

    atras(): void {
      this.viendo = false;
      this.selectedObj = null;
      window.scrollTo(0, 0);
      this.myFormPago.patchValue({ref_pago : ''});
      this.myFormPago.patchValue({metodo_pago : 0});
      this.myFormPago.patchValue({pedido_id : 0});
    }

    irARegistro(){
      this.router.navigate(['luvas/registro']);
    }

    login(): void { 
   
      this.loading = true;

      var datos= {
        email: this.myFormLogin.value.email,
        password: this.myFormLogin.value.password
      }

      this.http.post(this.rutaService.getRutaApi()+'login/app', datos)
         .toPromise()
         .then(
           data => { // Success
              console.log(data);
              this.data = data;

              this.loading = false;

              this.alerta_tipo = 'success';
              this.alerta_msg = 'Login Ok';
              this.alerta = true;
              setTimeout(()=>{this.alerta = false;},4000);

              /*Guardar datos del usuario en memoria*/
              var user = JSON.stringify(this.data.user);
              localStorage.setItem('luvas_user', user);

              var token = JSON.stringify(this.data.token);
              localStorage.setItem('luvas_token', token);

              this.user = this.data.user;
              this.setFormProfile();

              this.getMisPedidos();

              this.loginEventService.loginEventData.emit(1);

  
           },
           msg => { // Error
             msg.error = JSON.parse(msg.error);
             console.log(msg);
             console.log(msg.error.error);

             this.loading = false;

              this.alerta_tipo = 'danger';
              this.alerta_msg = msg.error.error;
              this.alerta = true;
              setTimeout(()=>{this.alerta = false;},4000);
             
           }
         );
    }

    setTransferencia(){
      this.myFormPago.patchValue({metodo_pago : 1});
    }

    setPayPal(){
      this.myFormPago.patchValue({metodo_pago : 2});
    }

    pagar(): void { 

      this.loading = true;

      var datos= {
        token: localStorage.getItem('luvas_token'),
        estado: 2,
        metodo_pago: this.myFormPago.value.metodo_pago,
        ref_pago: this.myFormPago.value.ref_pago
      }

      this.http.put(this.rutaService.getRutaApi()+'pedidos/'+this.myFormPago.value.pedido_id, datos)
         .toPromise()
         .then(
           data => { // Success
              console.log(data);
              this.data = data;

              this.loading = false;

              this.alerta_tipo = 'success';
              this.alerta_msg = this.data.message;
              this.alerta = true;
              setTimeout(()=>{this.alerta = false;},4000);

              this.selectedObj.estado = 'Payment';
              this.selectedObj.ref_pago = this.myFormPago.value.ref_pago;
              if (this.myFormPago.value.metodo_pago == 1) {
                this.selectedObj.metodo_pago = 'Transfer';
              }else if(this.myFormPago.value.metodo_pago == 2){
                this.selectedObj.metodo_pago = 'PayPal';
              }

              for (var i = 0; i < this.pedidos.length; i++) {
                if (this.pedidos[i].id == this.selectedObj.id) {
                  this.pedidos[i].estado = 'Payment';
                
                  if (this.myFormPago.value.metodo_pago == 1) {
                    this.pedidos[i].metodo_pago = 'Transfer';
                  }else if(this.myFormPago.value.metodo_pago == 2){
                    this.pedidos[i].metodo_pago = 'PayPal';
                  }
                }
              }

  
           },
           msg => { // Error
             msg.error = JSON.parse(msg.error);
             console.log(msg);
             console.log(msg.error.error);

             this.loading = false;

             //token invalido/ausente o token expiro
             if(msg.status == 400 || msg.status == 401){ 
                  //alert(msg.error.error);
                  //ir a login

                  this.alerta_tipo = 'warning';
                  this.alerta_msg = msg.error.error;
                  this.alerta_boton = true;
              }
              else { 
                  //alert(msg.error.error);

                  this.alerta_tipo = 'danger';
                  this.alerta_msg = msg.error.error;
                  this.alerta = true;
                  setTimeout(()=>{this.alerta = false;},4000);
              }
           }
         );
 
  }

  checkout(){
    //Mostrar modal de metodo de pago
    this.showModal2();
  }

    getBancos(): void {
      this.loading = true;

      this.http.get(this.rutaService.getRutaApi()+'bancos?token='+localStorage.getItem('luvas_token'))
         .toPromise()
         .then(
           data => { // Success
             console.log(data);
             this.data=data;
             this.bancos = this.data.bancos;

             this.loading = false;
             this.myModal2.show();

           },
           msg => { // Error
             msg.error = JSON.parse(msg.error);
             console.log(msg);
             console.log(msg.error.error);

             this.loading = false;

             //token invalido/ausente o token expiro
             if(msg.status == 400 || msg.status == 401){ 
                  //alert(msg.error.error);
                  //ir a login

                  this.alerta_tipo = 'warning';
                  this.alerta_msg = msg.error.error;
                  this.alerta_boton = true;

              }
              //sin categorias
              else if(msg.status == 404){ 
                  //alert(msg.error.error);

                  this.alerta_tipo = 'info';
                  this.alerta_msg = msg.error.error;
                  this.alerta = true;
                  setTimeout(()=>{this.alerta = false;},4000);
              }

           }
         );
    }

    setFormProfile(){
      this.myFormUpdateProfile.patchValue({email : this.user.email});
      //this.myFormUpdateProfile.patchValue({password : ''});
      //this.myFormUpdateProfile.patchValue({confirm : ''});
      this.myFormUpdateProfile.patchValue({nombre : this.user.nombre});
      this.myFormUpdateProfile.patchValue({telefono : this.user.telefono});
      this.myFormUpdateProfile.patchValue({direccion : this.user.direccion});
      //this.myFormUpdateProfile.patchValue({activo : ''});
      //this.myFormUpdateProfile.patchValue({tipo_usuario : 2});
      //this.myFormUpdateProfile.patchValue({tipo_registro : 1});
      this.myFormUpdateProfile.patchValue({imagen : this.user.imagen});
    }

    validarUpdate(): void {

      /*Verificar si hay un usuario logeado*/
      this.token = localStorage.getItem('luvas_token');
      var user_aux = localStorage.getItem('luvas_user');
      var user_login = JSON.parse(user_aux);

      if (!user_login || user_login == 'null' || !this.token || this.token == 'null') {

        this.showModal();

      }else{
        
        this.user = user_login;
        console.log(this.myFormUpdateProfile.value);

        if (
            this.myFormUpdateProfile.value.password != '' ||
            
            this.myFormUpdateProfile.value.confirm != '')  {

          if (this.myFormUpdateProfile.value.password != this.myFormUpdateProfile.value.confirm) {
            this.alerta_tipo = 'danger';
            this.alerta_msg = 'Passwords do not match.';
            this.alerta = true;
            setTimeout(()=>{this.alerta = false;},4000);
          }else{
            this.updateProfile();
          }

        }
        else{

          this.updateProfile();
        } 
      } 
    }

    updateProfile(){
      this.loading = true;

      var imgAux: any;
      
      if(this.imgUpload){
        imgAux = this.imgUpload; 
      }
      else{
        imgAux = this.myFormUpdateProfile.value.imagen;
      }

          var datos= {
            token: localStorage.getItem('luvas_token'),
            email: this.myFormUpdateProfile.value.email,
            password: this.myFormUpdateProfile.value.password,
            nombre: this.myFormUpdateProfile.value.nombre,
            telefono: this.myFormUpdateProfile.value.telefono,
            direccion: this.myFormUpdateProfile.value.direccion,
            //activo: this.myFormUpdateProfile.value.activo,
            //tipo_usuario: this.myFormUpdateProfile.value.tipo_usuario,
            //tipo_registro: this.myFormUpdateProfile.value.tipo_registro,
            imagen: imgAux
          }

          this.http.put(this.rutaService.getRutaApi()+'usuarios/'+this.user.id, datos)
             .toPromise()
             .then(
               data => { // Success
                  console.log(data);
                  this.data = data;

                  this.loading = false;

                  this.alerta_tipo = 'success';
                  this.alerta_msg = this.data.message;
                  this.alerta = true;
                  setTimeout(()=>{this.alerta = false;},4000);

                  /*Guardar datos del usuario en memoria*/
                  var user = JSON.stringify(this.data.usuario);
                  localStorage.setItem('luvas_user', user);

                  //var token = JSON.stringify(this.data.token);
                  //localStorage.setItem('luvas_user', token);

                  this.loginEventService.loginEventData.emit(1);

                  this.user = this.data.usuario;
      
               },
               msg => { // Error
                 msg.error = JSON.parse(msg.error);
                 console.log(msg);
                 console.log(msg.error.error);

                 this.loading = false;

                 //token invalido/ausente o token expiro
                 if(msg.status == 400 || msg.status == 401){ 
                      //alert(msg.error.error);
                      //ir a login

                      this.alerta_tipo = 'warning';
                      this.alerta_msg = msg.error.error;
                      this.alerta_boton = true;
                      this.mostrar = false;
                  }
                  else { 
                      //alert(msg.error.error);

                      this.alerta_tipo = 'danger';
                      this.alerta_msg = msg.error.error;
                      this.alerta = true;
                      setTimeout(()=>{this.alerta = false;},4000);
                  }
               }
             );
    }

    //Carga de img---<
    subirImagen(): void {
     
      this.loading = true;

      const formModel = this.prepareSave();

      this.http.post(this.rutaService.getRutaApi()+'imagenes?token='+localStorage.getItem('mouvers_token'), formModel)
         .toPromise()
         .then(
           data => { // Success
              console.log(data);
              this.data = data;
              this.imgUpload = this.data.imagen;
              this.myFormUpdateProfile.patchValue({imagen : this.imgUpload});

              //Solo admitimos imgenes.
               if (!this.fileIMG.type.match('image.*')) {
                    return;
               }

               var reader = new FileReader();

               reader.onload = (function(theFile) {
                   return function(e) {
                   // Creamos la imagen.
                    document.getElementById("list").innerHTML = ['<img class="thumb" src="', e.target.result, '" height="100px"/>'].join('');
                   };
               })(this.fileIMG);
     
               reader.readAsDataURL(this.fileIMG);

               this.clear = true;

              this.loading = false;
              //alert(this.data.message);
              this.alerta_tipo = 'success';
              this.alerta_msg = this.data.message;
              this.alerta = true;
              setTimeout(()=>{this.alerta = false;},4000);
               
           },
           msg => { // Error
             msg.error = JSON.parse(msg.error);
             console.log(msg);
             console.log(msg.error.error);

             this.loading = false;

             //token invalido/ausente o token expiro
             if(msg.status == 400 || msg.status == 401){ 
                  //alert(msg.error.error);
                  //ir a login
                  this.alerta_tipo = 'warning';
                  this.alerta_msg = msg.error.error;
                  this.alerta_boton = true;


              }
              else { 
                  //alert(msg.error.error);

                  this.alerta_tipo = 'danger';
                  this.alerta_msg = msg.error.error;
                  this.alerta = true;
                  setTimeout(()=>{this.alerta = false;},4000);
              }
           }
         );
    }

    private prepareSave(): any {
      let input = new FormData();
      input.append('imagen', this.fileIMG);
      input.append('carpeta', 'usuarios');
      input.append('url_imagen', this.rutaService.getRutaImages());
      return input;
    }

    onFileChange(event) {
      if(event.target.files.length > 0) {
        this.fileIMG = event.target.files[0];

        this.subirImagen();
      }
    }

    clearFile() {
      this.imgUpload = null;
      this.fileInput.nativeElement.value = '';

      this.clear = false;

      this.myFormUpdateProfile.patchValue({imagen : null});
    }
    //Carga de img--->

}



