import { Component, ElementRef, ChangeDetectionStrategy, ViewChild } from '@angular/core';

import { HttpClient, HttpParams } from '@angular/common/http';
import {Router} from '@angular/router';
import 'rxjs/add/operator/toPromise';

//import { ModalDirective } from 'ngx-bootstrap/modal/modal.component'; //modales
import { ModalDirective } from 'ngx-bootstrap/modal';

import { RutaService } from '../../services/ruta-service/ruta.service';

import { FormBuilder, FormArray, FormGroup, Validators  } from '@angular/forms';

//import {NgxPermissionsService, NgxRolesService} from 'ngx-permissions';

import { LoginEventService } from '../../events/login-event/login-event.service';

import { CartEventService } from '../../events/cart-event/cart-event.service';

import { CartService } from '../../services/cart-service/cart.service';

import { AuthService } from "angular4-social-login";
import { FacebookLoginProvider/*, GoogleLoginProvider, LinkedInLoginProvider*/ } from "angular4-social-login";

@Component({
  selector: 'header-luvas',
  templateUrl: './header-luvas.component.html',
  styleUrls : ['header-luvas.component.css']
})
export class HeaderLuvas {

  //Modal automatica
  @ViewChild('autoShownModal') public autoShownModal:ModalDirective;
  public isModalShown:boolean = false;
 
  public showModal():void {
    this.isModalShown = true;
  }
 
  public hideModal():void {
    this.autoShownModal.hide();
  }
 
  public onHidden():void {
    this.isModalShown = false;
  }

  public loading = false;
  private data:any;

  public alerta = false;
  public alerta_boton = false;
  public alerta_tipo: any; //success info warning danger  
  public alerta_msg: any;

  //Formularios
  myFormLogin: FormGroup;

  public admin = false;
  public email : any;
  public imagen = 'assets/images/icons/icon-header-01.png';
  //public itemsInCart : any;
  public showHideMessage: boolean = true;
  public showHideCart: boolean = true;

  public items: any;
  public itemsInCart: number;
  public total: number;

  constructor(private el: ElementRef,
          private http: HttpClient,
          private router: Router,
          private rutaService: RutaService,
          public fb: FormBuilder,
          public loginEventService: LoginEventService,
          public cartEventService: CartEventService,
          private cartService: CartService,
          private authService: AuthService) { 

      //Detectar un evento login
      this.loginEventService.loginEventData.subscribe(
          (data: any) => {
            //console.log(data); 
            this.checkLogin();
        });

      //Detectar un evento cart
      this.cartEventService.cartEventData.subscribe(
          (data: any) => {
            //console.log(data); 
            this.itemsInCart = data;

            this.items = this.cartService.getCartContents();
            //this.itemsInCart = this.cartService.getCartCount();
            this.total = this.cartService.getCartSubTotal();

        });

      this.myFormLogin = this.fb.group({
        email: ['', [Validators.required]],
        password: ['', [Validators.required]]
      });



  }

  //wait for the component to render completely
  ngOnInit(): void {

    //Esto es de la plantilla original
    var nativeElement: HTMLElement = this.el.nativeElement,
    parentElement: HTMLElement = nativeElement.parentElement;
    // move all children out of the element
    while (nativeElement.firstChild) {
      parentElement.insertBefore(nativeElement.firstChild, nativeElement);
    }
    // remove the empty element(the host)
    parentElement.removeChild(nativeElement);

    this.checkLogin();

    /*this.cartService.getProducts().subscribe(
      success => {
        this.itemsInCart = this.cartService.getCartCount();
        console.log('itemsInCart header: '+ this.itemsInCart);
        
      },
      error => {
        this.itemsInCart = this.cartService.getCartCount();
        console.log('itemsInCart header: '+ this.itemsInCart);
        
      }
    );*/

    //this.loading = true;
      this.cartService.getProducts().subscribe(
      success => {
        //this.loading = false;
        this.items = this.cartService.getCartContents();
        this.itemsInCart = this.cartService.getCartCount();
        this.total = this.cartService.getCartSubTotal();
        console.log(this.items);
        //console.log('itemsInCart cart: '+ this.itemsInCart);
        //this.cartEventService.cartEventData.emit(this.itemsInCart);
        
      },
      error => {
        //this.loading = false;
        this.items = this.cartService.getCartContents();
        this.itemsInCart = this.cartService.getCartCount();
        this.total = this.cartService.getCartSubTotal();
        console.log(this.items);
        //console.log('itemsInCart cart: '+ this.itemsInCart);
        //this.cartEventService.cartEventData.emit(this.itemsInCart);
        
      }
    );
    
  }

  signInWithFB(): void {
    this.authService.signIn(FacebookLoginProvider.PROVIDER_ID);
  }

  signOut(): void {
    this.authService.signOut();
  }

  slides = [
    {img: "http://placehold.it/350x150/000000"},
    {img: "http://placehold.it/350x150/111111"},
    {img: "http://placehold.it/350x150/333333"},
    {img: "http://placehold.it/350x150/666666"}
  ];
  slideConfig = {"slidesToShow": 4, "slidesToScroll": 4};

  addSlide() {
    this.slides.push({img: "http://placehold.it/350x150/777777"})
  }

  removeSlide() {
    this.slides.length = this.slides.length - 1;
  }

  afterChange(e) {
    console.log('afterChange');
  }

  //Alerta cuando se vence el token
  ok_alerta(): void {
    this.router.navigate(['luvas']);
  }

  cerrarAlerta(): void {
    this.alerta = false
  }

  public prueba():void {
    alert('Prueba');
  }

    irARegistro(){
      this.router.navigate(['luvas/registro']);
    }

    login(): void { 
   
      this.showHideMessage = true;

      this.loading = true;
      
      var datos= {
        email: this.myFormLogin.value.email,
        password: this.myFormLogin.value.password
      }

      this.http.post(this.rutaService.getRutaApi()+'login/app', datos)
         .toPromise()
         .then(
           data => { // Success
              console.log(data);
              this.data = data;

              this.loading = false;

              this.alerta_tipo = 'success';
              this.alerta_msg = 'Login Ok';
              this.alerta = true;
              setTimeout(()=>{this.alerta = false;},4000);

              /*Guardar datos del usuario en memoria*/
              var user = JSON.stringify(this.data.user);
              localStorage.setItem('luvas_user', user);

              var token = JSON.stringify(this.data.token);
              localStorage.setItem('luvas_token', token);

              if (this.data.user.tipo_usuario == 1) {
                this.admin = true;
                this.email = this.data.user.email;
                this.imagen = this.data.user.imagen;
              }else{
                this.admin = false;
                this.email = this.data.user.email;
                this.imagen = this.data.user.imagen;
              }

              if('/luvas/pedidos/cliente' == this.router.url){
                //console.log('recarga');
                window.location.reload();
              }
  
           },
           msg => { // Error
             msg.error = JSON.parse(msg.error);
             console.log(msg);
             console.log(msg.error.error);

             this.loading = false;

              this.alerta_tipo = 'danger';
              this.alerta_msg = msg.error.error;
              this.alerta = true;
              setTimeout(()=>{this.alerta = false;},4000);
             
           }
         );
    }

    showPanel(): void {
      this.showHideMessage = true;
      this.router.navigate(['dashboard']);
    }

    logout(){

      this.showHideMessage = true;

      localStorage.setItem('luvas_user', null);
      localStorage.setItem('luvas_token', null);
      this.admin = false;
      this.email = null;
      this.imagen = 'assets/images/icons/icon-header-01.png';
    }

    checkLogin(){
      /*Verifiacar si hay un usario logeado y su tipo*/
      var user = localStorage.getItem('luvas_user');
      var user_login = JSON.parse(user);

      if (user_login) {
        if (user_login.tipo_usuario == 1) {
          this.admin = true;
          this.email = user_login.email;
          this.imagen = user_login.imagen;
        }else{
          this.admin = false;
          this.email = user_login.email;
          this.imagen = user_login.imagen;
        }
      }
    }

    irAlCart() {
      this.showHideCart = true;
      this.router.navigateByUrl('/luvas/carrito');
    }

    changeShowMessage(){
      this.showHideMessage = !this.showHideMessage;
      this.showHideCart = true;
    }

    changeShowCart(){
      this.showHideCart = !this.showHideCart;
      this.showHideMessage = true;
    }


}


