import { Component, ChangeDetectionStrategy, ElementRef, ViewChild, OnDestroy } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import {Router} from '@angular/router';
import 'rxjs/add/operator/toPromise';

//import { ModalDirective } from 'ngx-bootstrap/modal/modal.component'; //modales
import { ModalDirective } from 'ngx-bootstrap/modal';

import { RutaService } from '../../../services/ruta-service/ruta.service';

import { FormBuilder, FormArray, FormGroup, Validators  } from '@angular/forms';

//import {NgxPermissionsService, NgxRolesService} from 'ngx-permissions';

import { NgxGalleryOptions, NgxGalleryImage, NgxGalleryAnimation } from 'ngx-gallery';

import { CartService } from '../../../services/cart-service/cart.service';

import { LoginEventService } from '../../../events/login-event/login-event.service';

import { CartEventService } from '../../../events/cart-event/cart-event.service';

@Component({
  templateUrl: 'cart.component.html',
  styleUrls: ['cart.component.css']
})
export class CartComponent /*implements OnInit, OnDestroy*/ {

  //Modal automatica
  @ViewChild('autoShownModal') public autoShownModal:ModalDirective;
  public isModalShown:boolean = false;
 
  public showModal():void {
    this.isModalShown = true;
  }
 
  public hideModal():void {
    this.autoShownModal.hide();
  }
 
  public onHidden():void {
    this.isModalShown = false;
  }

  //Modal automatica Seleccion de metodo de pago
  @ViewChild('autoShownModal2') public autoShownModal2:ModalDirective;
  public isModalShown2:boolean = false;
 
  public showModal2():void {
    this.isModalShown2 = true;
  }
 
  public hideModal2():void {
    this.autoShownModal2.hide();
  }
 
  public onHidden2():void {
    this.isModalShown2 = false;
  }

  //public myModal;
  @ViewChild('myModal2') public myModal2:ModalDirective; //Pago por transferencia
  @ViewChild('myModal3') public myModal3:ModalDirective; //Pago por PayPal

  public items: any;
  public itemsInCart: number;
  public total: number;

  public loading = false;
  private data:any;

  public alerta = false;
  public alerta_boton = false;
  public alerta_tipo: any; //success info warning danger  
  public alerta_msg: any;

  //Formularios
  myFormLogin: FormGroup;
  myFormPago: FormGroup;
  myFormEnvio: FormGroup;

  public bancos = [];

  constructor(private http: HttpClient,
      private router: Router,
      private rutaService: RutaService,
      public fb: FormBuilder,
      private cartService: CartService,
      public loginEventService: LoginEventService,
      public cartEventService: CartEventService) {

    this.items = this.cartService.getCartContents();
    //console.log(this.items);

    this.myFormLogin = this.fb.group({
        email: ['', [Validators.required]],
        password: ['', [Validators.required]]
      });

    this.myFormPago = this.fb.group({
        ref_pago: ['', [Validators.required]],
        metodo_pago: [0],
        pedido_id: [0]
      });

    this.myFormEnvio = this.fb.group({
        country: [''],
        state: [''],
        postcode: [''],
        address: ['']
      });

  }

  ngOnInit(): void {

    if(this.router.navigated) {
      window.scrollTo(0, 0);
    }

    /*Si hay un usuario logeado, tomar su direccion*/
    var user_aux = localStorage.getItem('luvas_user');
    var user_login = JSON.parse(user_aux);
    if (user_login && user_login != 'null') {
      this.myFormEnvio.patchValue({address : user_login.direccion});
    }
    
      this.loading = true;
      this.cartService.getProducts().subscribe(
      success => {
        this.loading = false;
        this.items = this.cartService.getCartContents();
        this.itemsInCart = this.cartService.getCartCount();
        this.total = this.cartService.getCartSubTotal();
        //console.log(this.items);
        console.log('itemsInCart cart: '+ this.itemsInCart);
        this.cartEventService.cartEventData.emit(this.itemsInCart);
        
      },
      error => {
        this.loading = false;
        this.items = this.cartService.getCartContents();
        this.itemsInCart = this.cartService.getCartCount();
        this.total = this.cartService.getCartSubTotal();
        //console.log(this.items);
        console.log('itemsInCart cart: '+ this.itemsInCart);
        this.cartEventService.cartEventData.emit(this.itemsInCart);
        
      }
    );
  }

  ngOnDestroy() {
    if(this.myFormPago.value.pedido_id != 0){
      this.cartService.deleteCar();
      this.cartEventService.cartEventData.emit(0);
    }
  }

  slides = [
    {img: "http://placehold.it/350x150/000000"},
    {img: "http://placehold.it/350x150/111111"},
    {img: "http://placehold.it/350x150/333333"},
    {img: "http://placehold.it/350x150/666666"}
  ];
  slideConfig = {"slidesToShow": 4, "slidesToScroll": 4};

  addSlide() {
    this.slides.push({img: "http://placehold.it/350x150/777777"})
  }

  removeSlide() {
    this.slides.length = this.slides.length - 1;
  }

  afterChange(e) {
    console.log('afterChange');
  }

  //Alerta cuando se vence el token
  ok_alerta(): void {
    this.router.navigate(['luvas']);
  }

  cerrarAlerta(): void {
    this.alerta = false
  }

    removeItem(item){
      /*Si no se ha generado el pedido lo puedo eliminar*/
      if (this.myFormPago.value.pedido_id == 0) {
        this.cartService.removeProduct(item).subscribe(
          success => {
            if (success) {
              this.items = this.cartService.getCartContents();
              this.itemsInCart = this.cartService.getCartCount();
              this.total = this.cartService.getCartSubTotal();
              this.cartEventService.cartEventData.emit(this.itemsInCart);
            } else {
              //this.presentToast('Ha ocurrido un error intenta de nuevo');
              alert('Ha ocurrido un error intenta de nuevo');
            }
          },
          error => {
            //this.presentToast(error);
            alert(error);
          }
        );
      }
    
  }

  updateCant(item){
    this.cartService.updateProduct(item,item.cantidad).subscribe(
      success => {
        if (success) {
          this.items = this.cartService.getCartContents();
          this.itemsInCart = this.cartService.getCartCount();
          this.total = this.cartService.getCartSubTotal();
          this.cartEventService.cartEventData.emit(this.itemsInCart);
        } else {
          //this.presentToast('Ha ocurrido un error intenta de nuevo');
          alert('Ha ocurrido un error intenta de nuevo');
        }
      },
      error => {
        //this.presentToast(error);
        alert(error);
      }
    );
  }

  restarCantidad(producto) {
    //console.log(producto);
    producto.cantidad = producto.cantidad - 1;
    this.updateCant(producto);
  }

  sumarCantidad(producto) {
    //console.log(producto);
    producto.cantidad = producto.cantidad + 1;
    this.updateCant(producto);
  }

  setCantidad(producto) {
    if (producto.cantidad < 1) {
      if (producto.colores[0].atributos[0].cantidad > 0) {
        producto.cantidad = 1;
      }else{
        producto.cantidad = 0;
      }  
    }

    if (producto.cantidad > producto.colores[0].atributos[0].cantidad) {
      producto.cantidad = producto.colores[0].atributos[0].cantidad;
    }

    setTimeout(()=>{this.updateCant(producto);},60);
  }

  checkout(): void {
    /*Verificar si hay un usuario logeado*/
    var token = localStorage.getItem('luvas_token');
    if (!this.cartService.getCartId() || !token || token == 'null') {
      this.showModal();
    }else if(this.myFormPago.value.pedido_id == 0){
      setTimeout(()=>{this.crearPedido();},60);
      
    }else if(this.myFormPago.value.pedido_id != 0){
      setTimeout(()=>{this.actualizarEnvioPedido();},60);
      
    }
  }

  actualizarEnvioPedido(): void { 

      this.loading = true;

      var datos= {
        token: localStorage.getItem('luvas_token'),
        /*subtotal: this.total,
        total: this.total,*/
        country: this.myFormEnvio.value.country,
        state: this.myFormEnvio.value.state,
        postcode: this.myFormEnvio.value.postcode,
        address: this.myFormEnvio.value.address,
        /*usuario_id: this.cartService.getCartId(),
        productos: JSON.stringify(this.items)*/
      }

      this.http.put(this.rutaService.getRutaApi()+'pedidos/'+this.myFormPago.value.pedido_id, datos)
         .toPromise()
         .then(
           data => { // Success
              console.log(data);
              this.data = data;

              this.loading = false;

              //Mostrar modal de metodo de pago
              this.showModal2();

              this.alerta_tipo = 'success';
              this.alerta_msg = this.data.message;
              this.alerta = true;
              setTimeout(()=>{this.alerta = false;},4000);

  
           },
           msg => { // Error
             msg.error = JSON.parse(msg.error);
             console.log(msg);
             console.log(msg.error.error);

             this.loading = false;

             //token invalido/ausente o token expiro
             if(msg.status == 400 || msg.status == 401){ 
                  //alert(msg.error.error);
                  //ir a login

                  this.alerta_tipo = 'warning';
                  this.alerta_msg = msg.error.error;
                  this.alerta_boton = true;
              }
              else { 
                  //alert(msg.error.error);

                  this.alerta_tipo = 'danger';
                  this.alerta_msg = msg.error.error;
                  this.alerta = true;
                  setTimeout(()=>{this.alerta = false;},4000);
              }
           }
         );
 
  }

  crearPedido(): void { 

      this.loading = true;

      var datos= {
        token: localStorage.getItem('luvas_token'),
        subtotal: this.total,
        total: this.total,
        country: this.myFormEnvio.value.country,
        state: this.myFormEnvio.value.state,
        postcode: this.myFormEnvio.value.postcode,
        address: this.myFormEnvio.value.address,
        usuario_id: this.cartService.getCartId(),
        productos: JSON.stringify(this.items)
      }

      this.http.post(this.rutaService.getRutaApi()+'pedidos', datos)
         .toPromise()
         .then(
           data => { // Success
              console.log(data);
              this.data = data;
              
              this.myFormPago.patchValue({pedido_id : this.data.pedido.id});

              this.loading = false;

              //Mostrar modal de metodo de pago
              this.showModal2();

              this.alerta_tipo = 'success';
              this.alerta_msg = this.data.message;
              this.alerta = true;
              setTimeout(()=>{this.alerta = false;},4000);

  
           },
           msg => { // Error
             msg.error = JSON.parse(msg.error);
             console.log(msg);
             console.log(msg.error.error);

             this.loading = false;

             //token invalido/ausente o token expiro
             if(msg.status == 400 || msg.status == 401){ 
                  //alert(msg.error.error);
                  //ir a login

                  this.alerta_tipo = 'warning';
                  this.alerta_msg = msg.error.error;
                  this.alerta_boton = true;
              }
              else { 
                  //alert(msg.error.error);

                  this.alerta_tipo = 'danger';
                  this.alerta_msg = msg.error.error;
                  this.alerta = true;
                  setTimeout(()=>{this.alerta = false;},4000);
              }
           }
         );
 
  }

    irARegistro(){
      this.router.navigate(['luvas/registro']);
    }

    login(): void { 
   
      this.loading = true;

      var datos= {
        email: this.myFormLogin.value.email,
        password: this.myFormLogin.value.password
      }

      this.http.post(this.rutaService.getRutaApi()+'login/app', datos)
         .toPromise()
         .then(
           data => { // Success
              console.log(data);
              this.data = data;

              this.loading = false;

              this.alerta_tipo = 'success';
              this.alerta_msg = 'Login Ok';
              this.alerta = true;
              setTimeout(()=>{this.alerta = false;},4000);

              this.myFormEnvio.patchValue({address : this.data.user.direccion});

              /*Guardar datos del usuario en memoria*/
              var user = JSON.stringify(this.data.user);
              localStorage.setItem('luvas_user', user);

              var token = JSON.stringify(this.data.token);
              localStorage.setItem('luvas_token', token);

              this.checkout();

              this.loginEventService.loginEventData.emit(1);

  
           },
           msg => { // Error
             msg.error = JSON.parse(msg.error);
             console.log(msg);
             console.log(msg.error.error);

             this.loading = false;

              this.alerta_tipo = 'danger';
              this.alerta_msg = msg.error.error;
              this.alerta = true;
              setTimeout(()=>{this.alerta = false;},4000);
             
           }
         );
    }

    setTransferencia(){
      this.myFormPago.patchValue({metodo_pago : 1});
    }

    setPayPal(){
      this.myFormPago.patchValue({metodo_pago : 2});
    }

    pagar(): void { 

      this.loading = true;

      var datos= {
        token: localStorage.getItem('luvas_token'),
        estado: 2,
        metodo_pago: this.myFormPago.value.metodo_pago,
        ref_pago: this.myFormPago.value.ref_pago,
        country: this.myFormEnvio.value.country,
        state: this.myFormEnvio.value.state,
        postcode: this.myFormEnvio.value.postcode,
        address: this.myFormEnvio.value.address,
      }

      this.http.put(this.rutaService.getRutaApi()+'pedidos/'+this.myFormPago.value.pedido_id, datos)
         .toPromise()
         .then(
           data => { // Success
              console.log(data);
              this.data = data;

              this.loading = false;

              this.alerta_tipo = 'success';
              this.alerta_msg = this.data.message;
              this.alerta = true;
              setTimeout(()=>{this.alerta = false;},4000);

              this.irAMisPedidos();

  
           },
           msg => { // Error
             msg.error = JSON.parse(msg.error);
             console.log(msg);
             console.log(msg.error.error);

             this.loading = false;

             //token invalido/ausente o token expiro
             if(msg.status == 400 || msg.status == 401){ 
                  //alert(msg.error.error);
                  //ir a login

                  this.alerta_tipo = 'warning';
                  this.alerta_msg = msg.error.error;
                  this.alerta_boton = true;
              }
              else { 
                  //alert(msg.error.error);

                  this.alerta_tipo = 'danger';
                  this.alerta_msg = msg.error.error;
                  this.alerta = true;
                  setTimeout(()=>{this.alerta = false;},4000);
              }
           }
         );
 
  }

  irAMisPedidos(){
    this.router.navigate(['luvas/pedidos/cliente']);
  }

  getBancos(): void {
      this.loading = true;

      this.http.get(this.rutaService.getRutaApi()+'bancos?token='+localStorage.getItem('luvas_token'))
         .toPromise()
         .then(
           data => { // Success
             console.log(data);
             this.data=data;
             this.bancos = this.data.bancos;

             this.loading = false;
             this.myModal2.show();

           },
           msg => { // Error
             msg.error = JSON.parse(msg.error);
             console.log(msg);
             console.log(msg.error.error);

             this.loading = false;

             //token invalido/ausente o token expiro
             if(msg.status == 400 || msg.status == 401){ 
                  //alert(msg.error.error);
                  //ir a login

                  this.alerta_tipo = 'warning';
                  this.alerta_msg = msg.error.error;
                  this.alerta_boton = true;

              }
              //sin categorias
              else if(msg.status == 404){ 
                  //alert(msg.error.error);

                  this.alerta_tipo = 'info';
                  this.alerta_msg = msg.error.error;
                  this.alerta = true;
                  setTimeout(()=>{this.alerta = false;},4000);
              }

           }
         );
    }

}



