import { NgModule } from '@angular/core';

// Import containers
import {
  FullLayoutLuvas
} from '../../containers-luvas';

const APP_CONTAINERS2 = [
  FullLayoutLuvas
]

// Import components
import {
  HeaderLuvas,
  FooterLuvas
} from '../../components-luvas';

const APP_COMPONENTS2 = [
  HeaderLuvas,
  FooterLuvas
]

//Importar componentes
import { HomeComponent } from './home/home.component';
import { ProductosComponent } from './productos/productos.component';
import { DetalleComponent } from './detalle/detalle.component';
import { BlogComponent } from './blog/blog.component';
import { BlogDetalleComponent } from './blog-detalle/blog-detalle.component';
import { CartComponent } from './cart/cart.component';
import { ContactoComponent } from './contacto/contacto.component';
import { RegistroComponent } from './registro/registro.component';
import { PedidosCliComponent } from './pedidoscli/pedidoscli.component';

import { LuvasRoutingModule } from './luvas-routing.module';

//Mis imports
import { BsDropdownModule } from 'ngx-bootstrap/dropdown';

import { FormsModule } from '@angular/forms';

// Modal Component
import { ModalModule } from 'ngx-bootstrap/modal';
import { CommonModule } from '@angular/common';

import { LoadingModule, ANIMATION_TYPES  } from 'ngx-loading';

//import { Ng2UploaderModule } from 'ng2-uploader';

import { AlertModule } from 'ngx-bootstrap/alert';

import { ReactiveFormsModule } from '@angular/forms';

//import { NgxPermissionsModule } from 'ngx-permissions';

import { NgxGalleryModule } from 'ngx-gallery';

@NgModule({
  imports: [ 
  NgxGalleryModule,
  //NgxPermissionsModule.forRoot(),
  ReactiveFormsModule,
  AlertModule.forRoot(),
  //Ng2UploaderModule,
  LuvasRoutingModule,
  CommonModule ,
  BsDropdownModule,
  FormsModule,
  ModalModule.forRoot(),
  LoadingModule.forRoot({
        animationType: ANIMATION_TYPES.chasingDots,
        backdropBackgroundColour: 'rgba(0,0,0,0.5)', 
        backdropBorderRadius: '4px',
        primaryColour: '#ffffff', 
        secondaryColour: '#ffffff', 
        tertiaryColour: '#ffffff',
        fullScreenBackdrop: true
    })
   ],
  declarations: [
    HomeComponent,
    ProductosComponent,
    DetalleComponent,
    BlogComponent,
    BlogDetalleComponent,
    CartComponent,
    ContactoComponent,
    PedidosCliComponent,
    RegistroComponent,
    ...APP_CONTAINERS2,
    ...APP_COMPONENTS2,
  ]
})
export class LuvasModule { }



