import { Component, ChangeDetectionStrategy, ElementRef, ViewChild, OnDestroy } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import {Router} from '@angular/router';
import 'rxjs/add/operator/toPromise';

//import { ModalDirective } from 'ngx-bootstrap/modal/modal.component'; //modales
import { ModalDirective } from 'ngx-bootstrap/modal';

import { RutaService } from '../../../services/ruta-service/ruta.service';

import { FormBuilder, FormArray, FormGroup, Validators  } from '@angular/forms';


@Component({
  templateUrl: 'blog.component.html',
  //styleUrls: ['blog.component.css']
})
export class BlogComponent /*implements OnInit, OnDestroy*/ {

  private data:any;
  public productList:any;

  public loading = false;
  public editando = false;
  public agregando = false;
  public mostrar = true;

  public alerta = false;
  public alerta_boton = false;
  public alerta_tipo: any; //success info warning danger  
  public alerta_msg: any;

  public blogs:any;
  public viendo = false;
  public blogDetalle:any;

  //Formularios
  myFormComment: FormGroup;

  public masVendidos:any;

  public itemsCopy:any;
  public searchTerm:any;

  constructor(private http: HttpClient,
      private router: Router,
      private rutaService: RutaService,
      public fb: FormBuilder) {

    this.myFormComment = this.fb.group({
        name: ['', [Validators.required]],
        email: ['', [Validators.required]],
        website: [''],
        comment: ['', [Validators.required]]
      });

  }

  ngOnInit(): void {
    if(this.router.navigated) {
      window.scrollTo(0, 0);
    }

    this.getProdsMasVendidos();

      this.loading = true;

      this.http.get(this.rutaService.getRutaApi()+'blogs?token='+localStorage.getItem('luvas_token'))
         .toPromise()
         .then(
           data => { // Success
             console.log(data);
             this.data=data;
             this.blogs = this.data.blogs;
             this.itemsCopy = this.blogs;

             //this.productList = this.data.blogs;
             //this.filteredItems = this.productList;
             this.filteredItems = this.blogs;
             this.init();

             /*Filtrar blog*/
             var blogFilter = localStorage.getItem('luvas_blogFilter');
             var blogAux = JSON.parse(blogFilter);
             if (blogAux) {
               this.ver(blogAux);
             }

             this.loading = false;
           },
           msg => { // Error
             msg.error = JSON.parse(msg.error);
             console.log(msg);
             console.log(msg.error.error);

             this.loading = false;

             //token invalido/ausente o token expiro
             if(msg.status == 400 || msg.status == 401){ 
                  //alert(msg.error.error);
                  //ir a login

                  this.alerta_tipo = 'warning';
                  this.alerta_msg = msg.error.error;
                  this.alerta_boton = true;
                  this.mostrar = false;

              }
              else { 
                  //alert(msg.error.error);

                  this.alerta_tipo = 'danger';
                  this.alerta_msg = msg.error.error;
                  this.alerta = true;
                  setTimeout(()=>{this.alerta = false;},4000);
              }

           }
         );
  }

  ngOnDestroy() {
    // acciones de destruccin
    localStorage.setItem('luvas_blogFilter', null);
   }

  slides = [
    {img: "http://placehold.it/350x150/000000"},
    {img: "http://placehold.it/350x150/111111"},
    {img: "http://placehold.it/350x150/333333"},
    {img: "http://placehold.it/350x150/666666"}
  ];
  slideConfig = {"slidesToShow": 4, "slidesToScroll": 4};

  addSlide() {
    this.slides.push({img: "http://placehold.it/350x150/777777"})
  }

  removeSlide() {
    this.slides.length = this.slides.length - 1;
  }

  afterChange(e) {
    console.log('afterChange');
  }

  //Alerta cuando se vence el token
  ok_alerta(): void {
    this.router.navigate(['luvas']);
  }

  cerrarAlerta(): void {
    this.alerta = false
  }

  ver(blog: any): void {

      this.loading = true;

      this.http.get(this.rutaService.getRutaApi()+'blogs/detalle/'+blog.id+'?token='+localStorage.getItem('luvas_token'))
         .toPromise()
         .then(
           data => { // Success
             console.log(data);
             this.data=data;
             this.blogDetalle = this.data.blog;

             this.viendo = true;
             this.loading = false;
             window.scrollTo(0, 0);
           },
           msg => { // Error
             msg.error = JSON.parse(msg.error);
             console.log(msg);
             console.log(msg.error.error);

             this.loading = false;

             //token invalido/ausente o token expiro
             if(msg.status == 400 || msg.status == 401){ 
                  //alert(msg.error.error);
                  //ir a login

                  this.alerta_tipo = 'warning';
                  this.alerta_msg = msg.error.error;
                  this.alerta_boton = true;
                  this.mostrar = false;

              }
              else { 
                  //alert(msg.error.error);

                  this.alerta_tipo = 'danger';
                  this.alerta_msg = msg.error.error;
                  this.alerta = true;
                  setTimeout(()=>{this.alerta = false;},4000);
              }

           }
         );
    }

  atras(): void {
    this.viendo = false;
    this.myFormComment.reset();
  }

  crearComment(): void {
      console.log(this.myFormComment.value);
      
      this.loading = true;

      var datos= {
        token: localStorage.getItem('luvas_token'),
        name: this.myFormComment.value.name,
        email: this.myFormComment.value.email,
        website: this.myFormComment.value.website,
        comment: this.myFormComment.value.comment,
        blog_id: this.blogDetalle.id
      }

      this.http.post(this.rutaService.getRutaApi()+'comments/blogs', datos)
         .toPromise()
         .then(
           data => { // Success
              console.log(data);
              this.data = data;

              this.blogDetalle.count_comments = this.blogDetalle.count_comments + 1;
              this.blogDetalle.comments.unshift(this.data.comment);
              this.myFormComment.reset();

              for (var i = 0; i < this.blogs.length; i++) {
                if (this.blogs[i].id == this.blogDetalle.id) {
                  this.blogs[i].count_comments = this.blogs[i].count_comments + 1;
                }
              }
              

              this.loading = false;

              this.alerta_tipo = 'success';
              this.alerta_msg = this.data.message;
              this.alerta = true;
              setTimeout(()=>{this.alerta = false;},4000);
  
           },
           msg => { // Error
             msg.error = JSON.parse(msg.error);
             console.log(msg);
             console.log(msg.error.error);

             this.loading = false;

             //token invalido/ausente o token expiro
             if(msg.status == 400 || msg.status == 401){ 
                  //alert(msg.error.error);
                  //ir a login

                  this.alerta_tipo = 'warning';
                  this.alerta_msg = msg.error.error;
                  this.alerta_boton = true;
                  this.mostrar = false;
              }
              else { 
                  //alert(msg.error.error);

                  this.alerta_tipo = 'danger';
                  this.alerta_msg = msg.error.error;
                  this.alerta = true;
                  setTimeout(()=>{this.alerta = false;},4000);
              }
           }
         );
    }

  /*Trae los  6productos mas vendidos*/
  getProdsMasVendidos(): void {
    //this.loading = true;
    this.http.get(this.rutaService.getRutaApi()+'productos/mas/vendidos?token='+localStorage.getItem('luvas_token'))
       .toPromise()
       .then(
         data => { // Success
           console.log(data);
           this.data = data;
           
           this.masVendidos = this.data.masVendidos;
           
           //this.loading = false;    
         },
         msg => { // Error
           msg.error = JSON.parse(msg.error);
           console.log(msg);
           console.log(msg.error.error);
           //this.loading = false;

           //token invalido/ausente o token expiro
           if(msg.status == 400 || msg.status == 401){ 
                //alert(msg.error.error);
                //ir a login

                this.alerta_tipo = 'warning';
                this.alerta_msg = msg.error.error;
                this.alerta_boton = true;
                this.mostrar = false;

            }
            else{ 
                //alert(msg.error.error);

                this.alerta_tipo = 'info';
                this.alerta_msg = msg.error.error;
                this.alerta = true;
                setTimeout(()=>{this.alerta = false;},4000);
            }

         }
       );
    }

    selectProducto(producto){
      var prod = JSON.stringify(producto)
      localStorage.setItem('luvas_prodFilter', prod);
      this.router.navigateByUrl('/luvas/productos');
    }

    /*Filtrar por titulo*/
    search(): void {
      let term = this.searchTerm.toLowerCase();
      this.blogs = this.itemsCopy.filter(function(tag) {
          return tag.titulo.toLowerCase().indexOf(term) >= 0;
      }); 
    }

    autoScroll(){
      window.scrollTo(0, 0);
    }

   //Tabla----<
   filteredItems : any;
   pages : number = 4;
   pageSize : number = 5;
   pageNumber : number = 0;
   currentIndex : number = 1;
   items: any;
   pagesIndex : Array<number>;
   pageStart : number = 1;
   inputName : string = '';

   init(){
         this.currentIndex = 1;
         this.pageStart = 1;
         this.pages = 4;

         this.pageNumber = parseInt(""+ (this.filteredItems.length / this.pageSize));
         if(this.filteredItems.length % this.pageSize != 0){
            this.pageNumber ++;
         }
    
         if(this.pageNumber  < this.pages){
               this.pages =  this.pageNumber;
         }
       
         this.refreshItems();
         console.log("this.pageNumber :  "+this.pageNumber);
   }

   FilterByName(){
      this.filteredItems = [];
      if(this.inputName != ""){
            for (var i = 0; i < this.blogs.length; ++i) {
              if (this.blogs[i].titulo.toUpperCase().indexOf(this.inputName.toUpperCase())>=0) {
                 this.filteredItems.push(this.blogs[i]);
              }/*else if (this.blogs[i].email.toUpperCase().indexOf(this.inputName.toUpperCase())>=0) {
                 this.filteredItems.push(this.blogs[i]);
              }*/
            }

      }else{
         this.filteredItems = this.blogs;
      }
      console.log(this.filteredItems);
      this.init();
   }
   fillArray(): any{
      var obj = new Array();
      for(var index = this.pageStart; index< this.pageStart + this.pages; index ++) {
                  obj.push(index);
      }
      return obj;
   }
   refreshItems(){
       this.items = this.filteredItems.slice((this.currentIndex - 1)*this.pageSize, (this.currentIndex) * this.pageSize);
       this.pagesIndex =  this.fillArray();
   }
   prevPage(){
      if(this.currentIndex>1){
         this.currentIndex --;
      } 
      if(this.currentIndex < this.pageStart){
         this.pageStart = this.currentIndex;
      }
      this.refreshItems();

      this.autoScroll();
   }
   nextPage(){
      if(this.currentIndex < this.pageNumber){
            this.currentIndex ++;
      }
      if(this.currentIndex >= (this.pageStart + this.pages)){
         this.pageStart = this.currentIndex - this.pages + 1;
      }
 
      this.refreshItems();

      this.autoScroll();
   }
    setPage(index : number){
         this.currentIndex = index;
         this.refreshItems();

         this.autoScroll();
    }
    //Tabla---->

}



