import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

// Import containers
import {
  FullLayoutLuvas
} from '../../containers-luvas';

//Importar componentes
import { HomeComponent } from './home/home.component';
import { ProductosComponent } from './productos/productos.component';
import { DetalleComponent } from './detalle/detalle.component';
import { BlogComponent } from './blog/blog.component';
import { BlogDetalleComponent } from './blog-detalle/blog-detalle.component';
import { CartComponent } from './cart/cart.component';
import { ContactoComponent } from './contacto/contacto.component';
import { RegistroComponent } from './registro/registro.component';
import { PedidosCliComponent } from './pedidoscli/pedidoscli.component';


const routes: Routes = [
  {
    path: '',
    redirectTo: 'home',
    pathMatch: 'full',
  },
   {
    path: '',
    component: FullLayoutLuvas,
    data: {
      title: 'Example Pages Luvas'
    },
    children: [
      {
        path: 'home',
        component: HomeComponent,
        data: {
          title: 'Page home'
        }
      },
      {
        path: 'productos',
        component: ProductosComponent,
        data: {
          title: 'Page productos'
        }
      },
      {
        path: 'detalle',
        component: DetalleComponent,
        data: {
          title: 'Page detalle'
        }
      },
      {
        path: 'blog',
        component: BlogComponent,
        data: {
          title: 'Page blog'
        }
      },
      {
        path: 'blog/detalle',
        component: BlogDetalleComponent,
        data: {
          title: 'Page blog detalle'
        }
      },
      {
        path: 'carrito',
        component: CartComponent,
        data: {
          title: 'Page carrito'
        }
      },
      {
        path: 'contacto',
        component: ContactoComponent,
        data: {
          title: 'Page contacto'
        }
      },
      {
        path: 'registro',
        component: RegistroComponent,
        data: {
          title: 'Page registro'
        }
      },
      {
        path: 'pedidos/cliente',
        component: PedidosCliComponent,
        data: {
          title: 'Page pedidos cliente'
        }
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class LuvasRoutingModule {}



